.pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 70px;
    border: 1px solid red;
}

.userProfile {
    display: flex;
    background-color: #fff;
    border-radius: 12px;
    box-shadow: 0px 2px 10px rgba(0,0,0, .25);
    width: 90%;
    height: 80px;
    align-items: center;
    justify-content: flex-start;
    border: none;
    padding: 20px;
    margin: 20px;
}
.userProfile:hover {
    background-color: rgba(152, 215, 233, 0.6);
}

.userProfile img {
    border-radius:50%;
    height: 80px;
}
.userProfile span {
    padding-left: 20px;
}

.userData {
    position: absolute;
    margin: 0px;
    padding: 0px;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    width: 80%;
    /* top:30px; */
    height: 70px;
}
.userFollowButton {
    position: absolute;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    width: 80%;
    margin-top: 20px;
    /* top:30px; */
}

.userFollowButton button {
    width: 150px;
    height: 30px;
    border-radius: 8px;
    border: none;
    font-weight: bolder;
    box-shadow: 0px 2px 10px rgba(0,0,0, .25);
    background-color: #ECECEC;
}
.userFollowButton button:hover {
    width: 150px;
    height: 30px;
    border-radius: 8px;
    border: none;
    box-shadow: 0px 2px 10px rgba(0,0,0, .25);
    background-color: #fff;
    transform: scale(1.05);
    transition: 0.1s ease-out;
    font-weight: bolder;
}

.pages {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    margin: 10px;
    height: 30px;
    border-radius: 50%;
    background: #fff;
    text-decoration: none;
}
.pages:hover {
    background-color: rgba(152, 215, 233, 0.6);
}
.pages:visited {
    color: black;
}