.messagesWrapper {
    display: grid;
    grid-template-areas:
    "ser";
    grid-template-columns: 12fr;
    background-color: #EFEFEF;
    height: 100%;
    min-height: 100vh;
    width: 100%;
    border: 1px solid red;
}

.message__texting {
    margin-top: 30px;
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    grid-area: ser;
    flex-wrap: wrap;
    
}

.message__texting__container {
    margin-top: 15px;
    margin-left: 30px;
    display: flex;
    flex-wrap: wrap;
    background-color: #fff;
    border-radius: 12px;
    box-shadow: 0px 2px 10px rgba(0,0,0, .25);
    width: 90%;
    height: 80px;
    align-items: center;
    justify-content: flex-start;
    border: none;
    
    border: 1px solid blue;
    
}
.message__texting__container:hover {
    display: flex;
    z-index: 1;
    background-color: rgba(152, 215, 233, 0.6);
    border-radius: 12px;
    box-shadow: 0px 2px 10px rgba(0,0,0, .25);
    width: 90%;
    height: 80px;
    align-items: center;
    justify-content: flex-start;
    
}

.message__texting__container__avatar img {
    display: flex;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin: 0px 0px 0px 20px;
    border: 2px solid lime;
}
.message__texting__container__avatar {
    background: none;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    z-index: 2;
}

.message__texting__window {
    display: flex;
    width: 80%;
    height: 100%;
    align-items: flex-end;
    flex-wrap: wrap;
    border: 1px solid red;
    
}

.message__texting__window span {
    margin: 0px 0px 20px 20px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}
